import React, { useState, useEffect } from 'react';
import { useEditor, EditorContent, BubbleMenu } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Placeholder from '@tiptap/extension-placeholder';
import { motion, AnimatePresence } from 'framer-motion';
import { useWindowSize, useLocalStorage } from 'react-use';
import {
  MagnifyingGlassIcon,
  PlusIcon,
  ChevronLeftIcon,
  UserCircleIcon,
  ArrowLeftOnRectangleIcon,
  Bars3Icon,
  TagIcon,
  FolderIcon,
  TrashIcon,
  ShareIcon,
  StarIcon as StarIconOutline,
  SunIcon,
  MoonIcon,
  AdjustmentsHorizontalIcon,
  ArrowPathIcon,
} from '@heroicons/react/24/outline';
import { StarIcon as StarIconSolid } from '@heroicons/react/24/solid';
import { useNavigate } from 'react-router-dom';
import { getNotes, createNote, updateNote, connectSocket, disconnectSocket, joinNoteSession, sendNoteChange, moveToTrash, getTrash, restoreFromTrash, permanentlyDeleteNote, cleanupOldTrashedNotes, isOfflineMode, toggleOfflineMode, syncOfflineChanges } from '../services/api';
import { Note, User } from '../types';
import ShareDialog from '../components/ShareDialog';

interface Category {
  id: string;
  name: string;
  color: string;
}

const CATEGORIES: Category[] = [
  { id: '1', name: 'Personal', color: 'bg-blue-500' },
  { id: '2', name: 'Work', color: 'bg-green-500' },
  { id: '3', name: 'Ideas', color: 'bg-purple-500' },
  { id: '4', name: 'Tasks', color: 'bg-yellow-500' },
];

const Notes: React.FC = () => {
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const [selectedNote, setSelectedNote] = useState<Note | null>(null);
  const [notes, setNotes] = useState<Note[]>([
    {
      id: '1',
      title: 'Welcome to CloudNotes',
      content: 'Start writing your first note!',
      updatedAt: new Date(),
      category: '1',
      tags: ['welcome', 'getting-started'],
      starred: false,
    },
  ]);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const [showTagInput, setShowTagInput] = useState(false);
  const [newTag, setNewTag] = useState('');
  const [viewMode, setViewMode] = useState<'all' | 'starred' | 'trash'>('all');
  const [theme, setTheme] = useLocalStorage<'light' | 'dark'>('theme', 'light');
  const [showSettings, setShowSettings] = useState(false);
  const { width } = useWindowSize();
  const isMobile = width < 768;
  const navigate = useNavigate();
  const [user, setUser] = useState<{ name: string; email: string } | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [deleteConfirmation, setDeleteConfirmation] = useState<{
    isOpen: boolean;
    noteId: string | null;
    noteTitle: string;
  }>({
    isOpen: false,
    noteId: null,
    noteTitle: '',
  });
  const [trashedNotes, setTrashedNotes] = useState<Note[]>([]);
  const [restoreConfirmation, setRestoreConfirmation] = useState<{
    isOpen: boolean;
    noteId: string | null;
    noteTitle: string;
  }>({
    isOpen: false,
    noteId: null,
    noteTitle: '',
  });
  const [selectedTrashItems, setSelectedTrashItems] = useState<string[]>([]);
  const [showBulkActions, setShowBulkActions] = useState(false);
  const [lastCleanupCheck, setLastCleanupCheck] = useLocalStorage<string>('lastCleanupCheck', '');
  const [showShareDialog, setShowShareDialog] = useState(false);
  const [collaborators, setCollaborators] = useState<{ id: string; name: string; email: string }[]>([]);
  const [isCollaborating, setIsCollaborating] = useState(false);
  const [isOffline, setIsOffline] = useState(isOfflineMode());
  const [isSyncing, setIsSyncing] = useState(false);

  useEffect(() => {
    if (theme === 'dark') {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, [theme]);

  useEffect(() => {
    // Check for authentication
    const token = localStorage.getItem('token');
    const userData = localStorage.getItem('user');
    
    if (!token || !userData) {
      navigate('/auth');
      return;
    }

    try {
      setUser(JSON.parse(userData));
    } catch (error) {
      console.error('Error parsing user data:', error);
      navigate('/auth');
    }
  }, [navigate]);

  const handleSignOut = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    navigate('/auth');
  };

  // Fetch notes on component mount
  useEffect(() => {
    const fetchNotes = async () => {
      try {
        setLoading(true);
        const response = await getNotes();
        setNotes(response.data);
      } catch (err: any) {
        setError(err.response?.data?.error || 'Failed to fetch notes');
        if (err.response?.status === 401) {
          navigate('/auth');
        }
      } finally {
        setLoading(false);
      }
    };

    fetchNotes();
  }, [navigate]);

  // Connect to WebSocket for real-time collaboration
  useEffect(() => {
    const socket = connectSocket();
    
    if (socket) {
      socket.on('note:update', (updatedNote: Note) => {
        setNotes(prevNotes => 
          prevNotes.map(note => 
            note.id === updatedNote.id ? updatedNote : note
          )
        );
        if (selectedNote?.id === updatedNote.id) {
          setSelectedNote(updatedNote);
          // Update editor content if the update came from another user
          if (updatedNote.content !== editor?.getHTML()) {
            editor?.commands.setContent(updatedNote.content);
          }
        }
      });

      socket.on('collaborator:join', (data: { user: { id: string; name: string; email: string } }) => {
        setCollaborators(prev => {
          if (!prev.find(c => c.id === data.user.id)) {
            return [...prev, data.user];
          }
          return prev;
        });
        setIsCollaborating(true);
      });

      socket.on('collaborator:leave', (data: { userId: string }) => {
        setCollaborators(prev => prev.filter(c => c.id !== data.userId));
        setIsCollaborating(prev => prev && collaborators.length > 1);
      });
    }

    return () => {
      if (socket) {
        socket.off('note:update');
        socket.off('collaborator:join');
        socket.off('collaborator:leave');
      }
    };
  }, [editor]);

  // Join note session when a note is selected
  useEffect(() => {
    if (selectedNote && user) {
      joinNoteSession(selectedNote.id, user.id);
    }
  }, [selectedNote, user]);

  const createNewNote = async () => {
    try {
      const response = await createNote({
        title: 'Untitled Note',
        content: '',
        category: selectedCategory || '1',
        tags: [],
      });
      
      const newNote = response.data;
      setNotes([newNote, ...notes]);
      setSelectedNote(newNote);
      editor?.commands.setContent('');
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to create note');
    }
  };

  const handleNoteUpdate = async (noteId: string, data: Partial<Note>) => {
    try {
      const response = await updateNote(noteId, data);
      const updatedNote = response.data;
      
      setNotes(notes.map(note => 
        note.id === noteId ? updatedNote : note
      ));
      
      if (selectedNote?.id === noteId) {
        setSelectedNote(updatedNote);
      }

      // Send changes to collaborators
      sendNoteChange(noteId, data);
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to update note');
    }
  };

  // Update the editor's onUpdate handler
  const editor = useEditor({
    extensions: [
      StarterKit,
      Placeholder.configure({
        placeholder: 'Start writing...',
      }),
    ],
    content: selectedNote?.content || '',
    onUpdate: ({ editor }) => {
      if (selectedNote) {
        const content = editor.getHTML();
        handleNoteUpdate(selectedNote.id, {
          content,
          updatedAt: new Date(),
        });
      }
    },
  });

  const handleDeleteNote = async () => {
    if (!deleteConfirmation.noteId) return;

    try {
      await deleteNoteApi(deleteConfirmation.noteId);
      setNotes(notes.filter(note => note.id !== deleteConfirmation.noteId));
      
      if (selectedNote?.id === deleteConfirmation.noteId) {
        setSelectedNote(null);
      }

      setDeleteConfirmation({ isOpen: false, noteId: null, noteTitle: '' });
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to delete note');
    }
  };

  const toggleStar = (noteId: string) => {
    setNotes(notes.map(note =>
      note.id === noteId ? { ...note, starred: !note.starred } : note
    ));
    if (selectedNote?.id === noteId) {
      setSelectedNote({ ...selectedNote, starred: !selectedNote.starred });
    }
  };

  const addTag = (noteId: string, tag: string) => {
    if (tag.trim()) {
      setNotes(notes.map(note =>
        note.id === noteId && !note.tags.includes(tag)
          ? { ...note, tags: [...note.tags, tag] }
          : note
      ));
      if (selectedNote?.id === noteId) {
        setSelectedNote({
          ...selectedNote,
          tags: [...selectedNote.tags, tag],
        });
      }
    }
    setNewTag('');
    setShowTagInput(false);
  };

  const removeTag = (noteId: string, tagToRemove: string) => {
    setNotes(notes.map(note =>
      note.id === noteId
        ? { ...note, tags: note.tags.filter(tag => tag !== tagToRemove) }
        : note
    ));
    if (selectedNote?.id === noteId) {
      setSelectedNote({
        ...selectedNote,
        tags: selectedNote.tags.filter(tag => tag !== tagToRemove),
      });
    }
  };

  const filteredNotes = notes.filter(note => {
    const matchesSearch = note.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      note.content.toLowerCase().includes(searchQuery.toLowerCase()) ||
      note.tags.some(tag => tag.toLowerCase().includes(searchQuery.toLowerCase()));
    const matchesCategory = !selectedCategory || note.category === selectedCategory;
    const matchesViewMode = viewMode === 'all' || (viewMode === 'starred' && note.starred);
    return matchesSearch && matchesCategory && matchesViewMode;
  });

  // Add cleanup check function
  const checkAndCleanupOldNotes = async () => {
    const now = new Date().toISOString();
    const oneDayMs = 24 * 60 * 60 * 1000;
    
    // Only run cleanup check once per day
    if (lastCleanupCheck && (new Date(now).getTime() - new Date(lastCleanupCheck).getTime()) < oneDayMs) {
      return;
    }

    try {
      await cleanupOldTrashedNotes();
      // Refresh trash list after cleanup
      const response = await getTrash();
      setTrashedNotes(response.data);
      setLastCleanupCheck(now);
    } catch (err: any) {
      console.error('Failed to cleanup old notes:', err);
    }
  };

  // Add cleanup check to the initial fetch
  useEffect(() => {
    const fetchTrash = async () => {
      try {
        const response = await getTrash();
        setTrashedNotes(response.data);
        await checkAndCleanupOldNotes();
      } catch (err: any) {
        setError(err.response?.data?.error || 'Failed to fetch trash');
      }
    };

    if (viewMode === 'trash') {
      fetchTrash();
    }
  }, [viewMode]);

  // Add remaining days indicator
  const getRemainingDays = (deletedAt: Date) => {
    const deleteDate = new Date(deletedAt);
    const expiryDate = new Date(deleteDate.getTime() + 31 * 24 * 60 * 60 * 1000);
    const now = new Date();
    const remainingDays = Math.ceil((expiryDate.getTime() - now.getTime()) / (24 * 60 * 60 * 1000));
    return Math.max(0, remainingDays);
  };

  const handleMoveToTrash = async (noteId: string) => {
    if (!deleteConfirmation.noteId) return;

    try {
      await moveToTrash(deleteConfirmation.noteId);
      setNotes(notes.filter(note => note.id !== deleteConfirmation.noteId));
      
      if (selectedNote?.id === deleteConfirmation.noteId) {
        setSelectedNote(null);
      }

      setDeleteConfirmation({ isOpen: false, noteId: null, noteTitle: '' });
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to move note to trash');
    }
  };

  const handleRestoreNote = async (noteId: string) => {
    try {
      await restoreFromTrash(noteId);
      const restoredNote = trashedNotes.find(note => note.id === noteId);
      if (restoredNote) {
        setNotes([restoredNote, ...notes]);
        setTrashedNotes(trashedNotes.filter(note => note.id !== noteId));
      }
      setRestoreConfirmation({ isOpen: false, noteId: null, noteTitle: '' });
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to restore note');
    }
  };

  const handlePermanentDelete = async (noteId: string) => {
    try {
      await permanentlyDeleteNote(noteId);
      setTrashedNotes(trashedNotes.filter(note => note.id !== noteId));
      setDeleteConfirmation({ isOpen: false, noteId: null, noteTitle: '' });
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to permanently delete note');
    }
  };

  // Add bulk action handlers
  const handleBulkRestore = async () => {
    try {
      await Promise.all(selectedTrashItems.map(id => restoreFromTrash(id)));
      const restoredNotes = trashedNotes.filter(note => selectedTrashItems.includes(note.id));
      setNotes([...notes, ...restoredNotes]);
      setTrashedNotes(trashedNotes.filter(note => !selectedTrashItems.includes(note.id)));
      setSelectedTrashItems([]);
      setShowBulkActions(false);
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to restore selected notes');
    }
  };

  const handleBulkDelete = async () => {
    try {
      await Promise.all(selectedTrashItems.map(id => permanentlyDeleteNote(id)));
      setTrashedNotes(trashedNotes.filter(note => !selectedTrashItems.includes(note.id)));
      setSelectedTrashItems([]);
      setShowBulkActions(false);
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to delete selected notes');
    }
  };

  const toggleTrashItemSelection = (noteId: string) => {
    setSelectedTrashItems(prev => 
      prev.includes(noteId) 
        ? prev.filter(id => id !== noteId)
        : [...prev, noteId]
    );
  };

  const toggleAllTrashItems = () => {
    if (selectedTrashItems.length === trashedNotes.length) {
      setSelectedTrashItems([]);
    } else {
      setSelectedTrashItems(trashedNotes.map(note => note.id));
    }
  };

  // Handle note sharing
  const handleShare = (shareRecord: NoteShare) => {
    if (selectedNote) {
      const updatedNote = {
        ...selectedNote,
        sharedWith: [...(selectedNote.sharedWith || []), shareRecord],
      };
      setSelectedNote(updatedNote);
      setNotes(prevNotes =>
        prevNotes.map(note =>
          note.id === selectedNote.id ? updatedNote : note
        )
      );
    }
  };

  // Add collaborator indicator to the editor toolbar
  const renderCollaboratorIndicator = () => {
    if (!isCollaborating) return null;

    return (
      <div className="flex items-center gap-1">
        <div className="flex -space-x-2">
          {collaborators.map((collaborator) => (
            <div
              key={collaborator.id}
              className="relative"
              title={`${collaborator.name} (${collaborator.email})`}
            >
              <div className="w-8 h-8 rounded-full bg-purple-600 flex items-center justify-center text-white font-medium border-2 border-white">
                {collaborator.name.charAt(0).toUpperCase()}
              </div>
            </div>
          ))}
        </div>
        {collaborators.length > 0 && (
          <span className="text-sm text-gray-500">
            {collaborators.length} active
          </span>
        )}
      </div>
    );
  };

  // Add offline mode detection
  useEffect(() => {
    const handleOnline = () => {
      setIsOffline(false);
      if (isOfflineMode()) {
        // Attempt to sync when coming back online
        handleSync();
      }
    };

    const handleOffline = () => {
      setIsOffline(true);
      toggleOfflineMode(true);
    };

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    // Check initial state
    setIsOffline(!navigator.onLine);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  // Add sync handler
  const handleSync = async () => {
    if (!navigator.onLine) {
      setError('Cannot sync while offline');
      return;
    }

    setIsSyncing(true);
    try {
      await syncOfflineChanges();
      // Refresh notes after sync
      const response = await getNotes();
      setNotes(response.data);
      toggleOfflineMode(false);
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to sync notes');
    } finally {
      setIsSyncing(false);
    }
  };

  return (
    <motion.div 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      className={`h-screen flex ${theme === 'dark' ? 'dark' : ''}`}
    >
      {/* Sidebar */}
      <motion.div
        initial={{ x: -288 }}
        animate={{ x: sidebarOpen ? 0 : -288 }}
        transition={{ type: "spring", stiffness: 300, damping: 30 }}
        className="bg-gray-900 dark:bg-gray-950 text-white w-72 flex-shrink-0 relative"
      >
        <div className="h-full flex flex-col">
          {/* Sidebar Header */}
          <motion.div 
            className="p-4 border-b border-gray-800"
            whileHover={{ backgroundColor: "rgba(255,255,255,0.05)" }}
          >
            <div className="flex items-center justify-between">
              <motion.h2 
                className="text-xl font-semibold bg-gradient-to-r from-purple-400 to-pink-500 bg-clip-text text-transparent"
                whileHover={{ scale: 1.02 }}
              >
                CloudNotes
              </motion.h2>
              <div className="flex items-center gap-2">
                <motion.button
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={() => setTheme(theme === 'light' ? 'dark' : 'light')}
                  className="p-2 hover:bg-gray-800 rounded-lg"
                >
                  {theme === 'light' ? (
                    <SunIcon className="h-5 w-5" />
                  ) : (
                    <MoonIcon className="h-5 w-5" />
                  )}
                </motion.button>
                <motion.button
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={() => setShowSettings(!showSettings)}
                  className="p-2 hover:bg-gray-800 rounded-lg"
                >
                  <AdjustmentsHorizontalIcon className="h-5 w-5" />
                </motion.button>
                {isMobile && (
                  <motion.button
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => setSidebarOpen(false)}
                    className="lg:hidden p-2 hover:bg-gray-800 rounded-lg"
                  >
                    <ChevronLeftIcon className="h-5 w-5" />
                  </motion.button>
                )}
              </div>
            </div>
          </motion.div>

          {/* Search */}
          <div className="p-4">
            <motion.div 
              className="relative"
              whileHover={{ scale: 1.01 }}
            >
              <MagnifyingGlassIcon className="h-5 w-5 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              <input
                type="text"
                placeholder="Search notes..."
                className="w-full bg-gray-800 dark:bg-gray-900 rounded-lg pl-10 pr-4 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-purple-500 transition-all duration-200"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </motion.div>
          </div>

          {/* View Toggle */}
          <div className="px-4 mb-4">
            <div className="flex rounded-lg overflow-hidden p-1 bg-gray-800 dark:bg-gray-900">
              <motion.button
                onClick={() => setViewMode('all')}
                className={`flex-1 py-2 text-sm font-medium rounded-md transition-colors duration-200 ${
                  viewMode === 'all'
                    ? 'bg-purple-600 text-white'
                    : 'text-gray-400 hover:text-white'
                }`}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                All Notes
              </motion.button>
              <motion.button
                onClick={() => setViewMode('starred')}
                className={`flex-1 py-2 text-sm font-medium rounded-md transition-colors duration-200 ${
                  viewMode === 'starred'
                    ? 'bg-purple-600 text-white'
                    : 'text-gray-400 hover:text-white'
                }`}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                Starred
              </motion.button>
              <motion.button
                onClick={() => setViewMode('trash')}
                className={`flex-1 py-2 text-sm font-medium rounded-md transition-colors duration-200 ${
                  viewMode === 'trash'
                    ? 'bg-purple-600 text-white'
                    : 'text-gray-400 hover:text-white'
                }`}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                Trash
              </motion.button>
            </div>
          </div>

          {/* Categories */}
          <div className="px-4 mb-4">
            <h3 className="text-sm font-medium text-gray-400 mb-2">Categories</h3>
            <motion.div 
              className="space-y-1"
              variants={{
                hidden: { opacity: 0 },
                show: {
                  opacity: 1,
                  transition: {
                    staggerChildren: 0.1
                  }
                }
              }}
              initial="hidden"
              animate="show"
            >
              {CATEGORIES.map((category) => (
                <motion.button
                  key={category.id}
                  onClick={() => setSelectedCategory(
                    selectedCategory === category.id ? null : category.id
                  )}
                  className={`w-full flex items-center px-3 py-2 rounded-lg text-sm transition-all duration-200 ${
                    selectedCategory === category.id
                      ? 'bg-gray-800 dark:bg-gray-900'
                      : 'hover:bg-gray-800/50 dark:hover:bg-gray-900/50'
                  }`}
                  whileHover={{ x: 4 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <motion.span 
                    className={`w-2 h-2 rounded-full ${category.color} mr-2`}
                    whileHover={{ scale: 1.2 }}
                  />
                  {category.name}
                </motion.button>
              ))}
            </motion.div>
          </div>

          {/* Note List */}
          <div className="flex-1 overflow-y-auto">
            <div className="px-4 py-2">
              <motion.button
                onClick={createNewNote}
                className="w-full flex items-center gap-2 px-4 py-2 bg-purple-600 hover:bg-purple-700 rounded-lg text-sm font-medium transition-colors duration-200"
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                <PlusIcon className="h-5 w-5" />
                New Note
              </motion.button>
            </div>
            <AnimatePresence>
              {viewMode === 'trash' ? (
                <>
                  {trashedNotes.length > 0 && (
                    <div className="sticky top-0 z-10 bg-gray-900 p-4 border-b border-gray-800">
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-3">
                          <input
                            type="checkbox"
                            checked={selectedTrashItems.length === trashedNotes.length}
                            onChange={toggleAllTrashItems}
                            className="w-4 h-4 rounded border-gray-600 text-purple-600 focus:ring-purple-500"
                          />
                          <span className="text-sm text-gray-400">
                            {selectedTrashItems.length === 0
                              ? `${trashedNotes.length} items`
                              : `${selectedTrashItems.length} selected`}
                          </span>
                        </div>
                        {selectedTrashItems.length > 0 && (
                          <div className="flex items-center gap-2">
                            <motion.button
                              whileHover={{ scale: 1.02 }}
                              whileTap={{ scale: 0.98 }}
                              onClick={handleBulkRestore}
                              className="px-3 py-1 text-sm bg-green-600 hover:bg-green-700 text-white rounded-lg transition-colors duration-200"
                            >
                              Restore Selected
                            </motion.button>
                            <motion.button
                              whileHover={{ scale: 1.02 }}
                              whileTap={{ scale: 0.98 }}
                              onClick={handleBulkDelete}
                              className="px-3 py-1 text-sm bg-red-600 hover:bg-red-700 text-white rounded-lg transition-colors duration-200"
                            >
                              Delete Selected
                            </motion.button>
                          </div>
                        )}
                      </div>
                      <p className="text-xs text-gray-500 mt-2">
                        Notes are automatically deleted after 31 days in trash
                      </p>
                    </div>
                  )}
                  {trashedNotes.length > 0 ? (
                    trashedNotes.map((note) => (
                      <motion.div
                        key={note.id}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, x: -100 }}
                        className="group relative p-3 mx-2 rounded-lg mb-1 bg-gray-800/50 dark:bg-gray-900/50"
                      >
                        <div className="flex items-center gap-3">
                          <input
                            type="checkbox"
                            checked={selectedTrashItems.includes(note.id)}
                            onChange={() => toggleTrashItemSelection(note.id)}
                            className="w-4 h-4 rounded border-gray-600 text-purple-600 focus:ring-purple-500"
                          />
                          <div className="flex-1">
                            <h3 className="font-medium text-gray-400">{note.title}</h3>
                            <div className="flex items-center gap-2 text-sm text-gray-500">
                              <span>Deleted {new Date(note.deletedAt!).toLocaleDateString()}</span>
                              <span className="text-xs px-2 py-0.5 rounded-full bg-gray-700">
                                {getRemainingDays(note.deletedAt!)} days left
                              </span>
                            </div>
                          </div>
                          <div className="flex items-center gap-2">
                            <motion.button
                              whileHover={{ scale: 1.1 }}
                              whileTap={{ scale: 0.9 }}
                              onClick={() => {
                                setRestoreConfirmation({
                                  isOpen: true,
                                  noteId: note.id,
                                  noteTitle: note.title,
                                });
                              }}
                              className="p-2 hover:bg-gray-700 rounded-lg text-green-500"
                            >
                              <ArrowPathIcon className="h-5 w-5" />
                            </motion.button>
                            <motion.button
                              whileHover={{ scale: 1.1 }}
                              whileTap={{ scale: 0.9 }}
                              onClick={() => {
                                setDeleteConfirmation({
                                  isOpen: true,
                                  noteId: note.id,
                                  noteTitle: note.title,
                                });
                              }}
                              className="p-2 hover:bg-gray-700 rounded-lg text-red-500"
                            >
                              <TrashIcon className="h-5 w-5" />
                            </motion.button>
                          </div>
                        </div>
                      </motion.div>
                    ))
                  ) : (
                    <div className="flex flex-col items-center justify-center h-full text-gray-500">
                      <TrashIcon className="h-12 w-12 mb-2" />
                      <p>Trash is empty</p>
                    </div>
                  )}
                </>
              ) : (
                <AnimatePresence>
                  {filteredNotes.map((note, index) => (
                    <motion.div
                      key={note.id}
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      exit={{ opacity: 0, x: -100 }}
                      transition={{ delay: index * 0.05 }}
                      className={`group relative p-3 mx-2 rounded-lg mb-1 transition-all duration-200 ${
                        selectedNote?.id === note.id
                          ? 'bg-gray-800 dark:bg-gray-900'
                          : 'hover:bg-gray-800/50 dark:hover:bg-gray-900/50'
                      }`}
                    >
                      <motion.button
                        onClick={() => {
                          setSelectedNote(note);
                          editor?.commands.setContent(note.content);
                        }}
                        className="w-full text-left"
                        whileHover={{ scale: 1.01 }}
                      >
                        <div className="flex items-center justify-between mb-1">
                          <h3 className="font-medium truncate">{note.title}</h3>
                          <motion.button
                            onClick={(e) => {
                              e.stopPropagation();
                              toggleStar(note.id);
                            }}
                            className="text-yellow-500"
                            whileHover={{ scale: 1.2 }}
                            whileTap={{ scale: 0.9 }}
                          >
                            {note.starred ? (
                              <StarIconSolid className="h-5 w-5" />
                            ) : (
                              <StarIconOutline className="h-5 w-5 opacity-0 group-hover:opacity-100 transition-opacity duration-200" />
                            )}
                          </motion.button>
                        </div>
                        <div className="flex items-center text-sm text-gray-400 mb-2">
                          <motion.span 
                            className={`w-2 h-2 rounded-full ${
                              CATEGORIES.find(c => c.id === note.category)?.color
                            } mr-2`}
                            whileHover={{ scale: 1.2 }}
                          />
                          {CATEGORIES.find(c => c.id === note.category)?.name}
                        </div>
                        {note.tags.length > 0 && (
                          <motion.div 
                            className="flex flex-wrap gap-1 mb-2"
                            variants={{
                              hidden: { opacity: 0 },
                              show: {
                                opacity: 1,
                                transition: {
                                  staggerChildren: 0.05
                                }
                              }
                            }}
                            initial="hidden"
                            animate="show"
                          >
                            {note.tags.map((tag) => (
                              <motion.span
                                key={tag}
                                className="px-2 py-1 text-xs bg-gray-700 dark:bg-gray-800 rounded-full"
                                whileHover={{ scale: 1.05 }}
                              >
                                {tag}
                              </motion.span>
                            ))}
                          </motion.div>
                        )}
                        <p className="text-sm text-gray-400">
                          {new Date(note.updatedAt).toLocaleDateString()}
                        </p>
                      </motion.button>
                    </motion.div>
                  ))}
                </AnimatePresence>
              )}
            </AnimatePresence>
          </div>

          {/* User Menu */}
          <motion.div 
            className="p-4 border-t border-gray-800"
            whileHover={{ backgroundColor: "rgba(255,255,255,0.05)" }}
          >
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <motion.div 
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.9 }}
                  className="relative"
                >
                  {user?.name ? (
                    <div className="w-8 h-8 rounded-full bg-purple-600 flex items-center justify-center text-white font-medium">
                      {user.name.charAt(0).toUpperCase()}
                    </div>
                  ) : (
                    <UserCircleIcon className="h-8 w-8" />
                  )}
                </motion.div>
                <div className="text-sm">
                  <p className="font-medium">{user?.name || 'Loading...'}</p>
                  <p className="text-gray-400">{user?.email || 'Loading...'}</p>
                </div>
              </div>
              <motion.button 
                onClick={handleSignOut}
                className="p-2 hover:bg-gray-800 rounded-lg transition-colors duration-200 group"
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
              >
                <ArrowLeftOnRectangleIcon className="h-5 w-5 group-hover:text-red-400 transition-colors duration-200" />
              </motion.button>
            </div>
          </motion.div>
        </div>
      </motion.div>

      {/* Settings Panel */}
      <AnimatePresence>
        {showSettings && (
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -20 }}
            className="absolute left-72 top-0 h-full w-64 bg-white dark:bg-gray-900 border-r border-gray-200 dark:border-gray-800 z-50"
          >
            <div className="p-4">
              <h2 className="text-lg font-semibold mb-4">Settings</h2>
              {/* Add settings options here */}
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Main Content */}
      <motion.div 
        className="flex-1 flex flex-col overflow-hidden bg-white dark:bg-gray-900"
        initial={false}
        animate={{ marginLeft: showSettings ? '16rem' : '0' }}
      >
        {/* Top Bar */}
        <div className="bg-white border-b">
          <div className="flex items-center justify-between px-4 py-3">
            <button
              onClick={() => setSidebarOpen(true)}
              className={`lg:hidden ${sidebarOpen ? 'hidden' : 'block'}`}
            >
              <Bars3Icon className="h-6 w-6" />
            </button>
            {selectedNote && (
              <div className="flex-1 px-4">
                <div className="flex items-center justify-between mb-2">
                  <input
                    type="text"
                    value={selectedNote.title}
                    onChange={(e) => {
                      handleNoteUpdate(selectedNote.id, {
                        title: e.target.value,
                        updatedAt: new Date(),
                      });
                    }}
                    placeholder="Note title..."
                    className="w-full text-lg font-medium focus:outline-none"
                  />
                  <div className="flex items-center gap-2">
                    <button
                      onClick={() => toggleStar(selectedNote.id)}
                      className="p-2 hover:bg-gray-100 rounded-lg text-yellow-500"
                    >
                      {selectedNote.starred ? (
                        <StarIconSolid className="h-5 w-5" />
                      ) : (
                        <StarIconOutline className="h-5 w-5" />
                      )}
                    </button>
                    <button
                      onClick={() => setShowTagInput(!showTagInput)}
                      className="p-2 hover:bg-gray-100 rounded-lg text-gray-500"
                    >
                      <TagIcon className="h-5 w-5" />
                    </button>
                    <select
                      value={selectedNote.category}
                      onChange={(e) => {
                        handleNoteUpdate(selectedNote.id, {
                          category: e.target.value,
                          updatedAt: new Date(),
                        });
                      }}
                      className="p-2 rounded-lg border border-gray-200 text-sm"
                    >
                      {CATEGORIES.map((category) => (
                        <option key={category.id} value={category.id}>
                          {category.name}
                        </option>
                      ))}
                    </select>
                    <button className="p-2 hover:bg-gray-100 rounded-lg text-gray-500">
                      <ShareIcon className="h-5 w-5" />
                    </button>
                    <button
                      onClick={() => {
                        if (selectedNote) {
                          setDeleteConfirmation({
                            isOpen: true,
                            noteId: selectedNote.id,
                            noteTitle: selectedNote.title,
                          });
                        }
                      }}
                      className="p-2 hover:bg-gray-100 rounded-lg text-red-500"
                    >
                      <TrashIcon className="h-5 w-5" />
                    </button>
                  </div>
                </div>
                {showTagInput && (
                  <div className="flex items-center gap-2 mb-2">
                    <input
                      type="text"
                      value={newTag}
                      onChange={(e) => setNewTag(e.target.value)}
                      onKeyPress={(e) => {
                        if (e.key === 'Enter') {
                          addTag(selectedNote.id, newTag);
                        }
                      }}
                      placeholder="Add tag..."
                      className="flex-1 px-3 py-1 text-sm border border-gray-200 rounded-lg focus:outline-none focus:border-purple-500"
                    />
                    <button
                      onClick={() => addTag(selectedNote.id, newTag)}
                      className="px-3 py-1 text-sm bg-purple-600 text-white rounded-lg hover:bg-purple-700"
                    >
                      Add
                    </button>
                  </div>
                )}
                {selectedNote.tags.length > 0 && (
                  <div className="flex flex-wrap gap-1 mb-2">
                    {selectedNote.tags.map((tag) => (
                      <span
                        key={tag}
                        className="group flex items-center gap-1 px-2 py-1 text-xs bg-gray-100 rounded-full"
                      >
                        {tag}
                        <button
                          onClick={() => removeTag(selectedNote.id, tag)}
                          className="opacity-0 group-hover:opacity-100 text-gray-400 hover:text-gray-600"
                        >
                          
                        </button>
                      </span>
                    ))}
                  </div>
                )}
                <p className="text-sm text-gray-500">
                  Last updated {new Date(selectedNote.updatedAt).toLocaleString()}
                </p>
              </div>
            )}
          </div>
        </div>

        {/* Editor */}
        <div className="flex-1 overflow-y-auto bg-white">
          {selectedNote ? (
            <>
              {editor && (
                <BubbleMenu editor={editor} tippyOptions={{ duration: 100 }}>
                  <div className="flex items-center gap-1 px-2 py-1 bg-gray-900 rounded-lg shadow-lg">
                    <button
                      onClick={() => editor.chain().focus().toggleBold().run()}
                      className={`p-1 rounded ${
                        editor.isActive('bold') ? 'bg-gray-700' : 'hover:bg-gray-800'
                      }`}
                    >
                      <span className="font-bold text-white">B</span>
                    </button>
                    <button
                      onClick={() => editor.chain().focus().toggleItalic().run()}
                      className={`p-1 rounded ${
                        editor.isActive('italic') ? 'bg-gray-700' : 'hover:bg-gray-800'
                      }`}
                    >
                      <span className="italic text-white">I</span>
                    </button>
                    <button
                      onClick={() => editor.chain().focus().toggleStrike().run()}
                      className={`p-1 rounded ${
                        editor.isActive('strike') ? 'bg-gray-700' : 'hover:bg-gray-800'
                      }`}
                    >
                      <span className="line-through text-white">S</span>
                    </button>
                    <button
                      onClick={() => editor.chain().focus().toggleCode().run()}
                      className={`p-1 rounded ${
                        editor.isActive('code') ? 'bg-gray-700' : 'hover:bg-gray-800'
                      }`}
                    >
                      <span className="font-mono text-white">{`</>`}</span>
                    </button>
                  </div>
                </BubbleMenu>
              )}
              <div className="max-w-4xl mx-auto p-8">
                <EditorContent editor={editor} className="prose max-w-none" />
              </div>
            </>
          ) : (
            <div className="h-full flex items-center justify-center text-gray-400">
              <div className="text-center">
                <p className="text-xl font-medium mb-2">No note selected</p>
                <button
                  onClick={createNewNote}
                  className="text-purple-600 hover:text-purple-700 font-medium"
                >
                  Create a new note
                </button>
              </div>
            </div>
          )}
        </div>
      </motion.div>

      {/* Delete Confirmation Dialog */}
      {deleteConfirmation.isOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            className="bg-white dark:bg-gray-800 rounded-lg p-6 max-w-md w-full mx-4 shadow-xl"
          >
            <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
              Delete Note
            </h3>
            <p className="text-gray-500 dark:text-gray-400 mb-4">
              Are you sure you want to permanently delete "{deleteConfirmation.noteTitle}"? This action cannot be undone.
            </p>
            <div className="flex justify-end gap-3">
              <motion.button
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                onClick={() => setDeleteConfirmation({ isOpen: false, noteId: null, noteTitle: '' })}
                className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors duration-200"
              >
                Cancel
              </motion.button>
              <motion.button
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                onClick={handlePermanentDelete}
                className="px-4 py-2 text-sm font-medium text-white bg-red-600 hover:bg-red-700 rounded-lg transition-colors duration-200"
              >
                Delete
              </motion.button>
            </div>
          </motion.div>
        </div>
      )}

      {/* Restore Confirmation Dialog */}
      {restoreConfirmation.isOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            className="bg-white dark:bg-gray-800 rounded-lg p-6 max-w-md w-full mx-4 shadow-xl"
          >
            <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
              Restore Note
            </h3>
            <p className="text-gray-500 dark:text-gray-400 mb-4">
              Are you sure you want to restore "{restoreConfirmation.noteTitle}"?
            </p>
            <div className="flex justify-end gap-3">
              <motion.button
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                onClick={() => setRestoreConfirmation({ isOpen: false, noteId: null, noteTitle: '' })}
                className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 hover:bg-gray-200 rounded-lg"
              >
                Cancel
              </motion.button>
              <motion.button
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                onClick={() => restoreConfirmation.noteId && handleRestoreNote(restoreConfirmation.noteId)}
                className="px-4 py-2 text-sm font-medium text-white bg-green-600 hover:bg-green-700 rounded-lg"
              >
                Restore
              </motion.button>
            </div>
          </motion.div>
        </div>
      )}

      {/* Share Dialog */}
      {selectedNote && (
        <ShareDialog
          isOpen={showShareDialog}
          onClose={() => setShowShareDialog(false)}
          note={selectedNote}
          onShare={handleShare}
        />
      )}
    </motion.div>
  );
};

export default Notes; 