import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { XMarkIcon } from '@heroicons/react/24/outline';
import { shareNote } from '../services/api';
import { Note, NoteShare } from '../types';

interface ShareDialogProps {
  isOpen: boolean;
  onClose: () => void;
  note: Note;
  onShare: (shareRecord: NoteShare) => void;
}

const ShareDialog: React.FC<ShareDialogProps> = ({ isOpen, onClose, note, onShare }) => {
  const [email, setEmail] = useState('');
  const [canEdit, setCanEdit] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  const handleShare = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setLoading(true);

    try {
      const response = await shareNote(note.id, { email, canEdit });
      onShare(response.data);
      setEmail('');
      setCanEdit(false);
      onClose();
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to share note');
    } finally {
      setLoading(false);
    }
  };

  return (
    <AnimatePresence>
      {isOpen && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50"
          onClick={onClose}
        >
          <motion.div
            initial={{ scale: 0.95, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            exit={{ scale: 0.95, opacity: 0 }}
            className="bg-white dark:bg-gray-800 rounded-lg p-6 w-full max-w-md"
            onClick={e => e.stopPropagation()}
          >
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-xl font-semibold text-gray-900 dark:text-white">
                Share Note
              </h2>
              <button
                onClick={onClose}
                className="text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200"
              >
                <XMarkIcon className="h-6 w-6" />
              </button>
            </div>

            <div className="mb-4">
              <h3 className="font-medium text-gray-900 dark:text-white mb-2">
                {note.title}
              </h3>
              {note.sharedWith && note.sharedWith.length > 0 && (
                <div className="mb-4">
                  <h4 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-2">
                    Shared with:
                  </h4>
                  <div className="space-y-2">
                    {note.sharedWith.map(share => (
                      <div
                        key={share.id}
                        className="flex items-center justify-between text-sm"
                      >
                        <span className="text-gray-900 dark:text-white">
                          {share.user?.email}
                        </span>
                        <span className="text-gray-500 dark:text-gray-400">
                          {share.canEdit ? 'Can edit' : 'Can view'}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>

            <form onSubmit={handleShare} className="space-y-4">
              {error && (
                <div className="text-red-500 text-sm bg-red-50 dark:bg-red-900/20 p-2 rounded">
                  {error}
                </div>
              )}

              <div>
                <label
                  htmlFor="email"
                  className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1"
                >
                  Email address
                </label>
                <input
                  type="email"
                  id="email"
                  value={email}
                  onChange={e => setEmail(e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 dark:bg-gray-700"
                  placeholder="Enter email address"
                  required
                />
              </div>

              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="canEdit"
                  checked={canEdit}
                  onChange={e => setCanEdit(e.target.checked)}
                  className="h-4 w-4 text-purple-600 focus:ring-purple-500 border-gray-300 rounded"
                />
                <label
                  htmlFor="canEdit"
                  className="ml-2 block text-sm text-gray-700 dark:text-gray-300"
                >
                  Allow editing
                </label>
              </div>

              <div className="flex justify-end">
                <motion.button
                  type="submit"
                  disabled={loading}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  className={`px-4 py-2 bg-purple-600 text-white rounded-lg ${
                    loading ? 'opacity-70 cursor-not-allowed' : 'hover:bg-purple-700'
                  }`}
                >
                  {loading ? (
                    <div className="flex items-center">
                      <svg
                        className="animate-spin -ml-1 mr-2 h-4 w-4 text-white"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                      >
                        <circle
                          className="opacity-25"
                          cx="12"
                          cy="12"
                          r="10"
                          stroke="currentColor"
                          strokeWidth="4"
                        />
                        <path
                          className="opacity-75"
                          fill="currentColor"
                          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                        />
                      </svg>
                      Sharing...
                    </div>
                  ) : (
                    'Share'
                  )}
                </motion.button>
              </div>
            </form>
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
};

export default ShareDialog; 