import axios from 'axios';
import { io, Socket } from 'socket.io-client';
import { offlineDb } from './offlineDb';
import { Note } from '../types';

const API_URL = 'http://localhost:5000/api';

const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Add token to requests if it exists
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Add offline handling to response interceptor
api.interceptors.response.use(
  (response) => response,
  async (error) => {
    if (!error.response && error.message === 'Network Error') {
      // We're offline
      const offlineMode = localStorage.getItem('offlineMode') === 'true';
      if (offlineMode) {
        // Handle offline requests
        const { method, url, data } = error.config;
        
        if (url.includes('/notes')) {
          const noteId = url.split('/').pop();
          
          if (method === 'GET' && url === '/notes') {
            // Return cached notes
            const notes = await offlineDb.getAllNotes();
            return { data: notes };
          }
          
          if (method === 'POST') {
            // Save new note to offline storage
            const noteData = JSON.parse(data);
            const note = {
              ...noteData,
              id: `offline-${Date.now()}`,
              createdAt: new Date(),
              updatedAt: new Date(),
            };
            await offlineDb.saveNote(note);
            await offlineDb.saveOfflineChange({
              noteId: note.id,
              type: 'create',
              data: note,
              timestamp: Date.now(),
            });
            return { data: note };
          }
          
          if (method === 'PUT') {
            // Update note in offline storage
            const noteData = JSON.parse(data);
            const existingNote = await offlineDb.getNote(noteId);
            if (existingNote) {
              const updatedNote = {
                ...existingNote,
                ...noteData,
                updatedAt: new Date(),
              };
              await offlineDb.saveNote(updatedNote);
              await offlineDb.saveOfflineChange({
                noteId,
                type: 'update',
                data: noteData,
                timestamp: Date.now(),
              });
              return { data: updatedNote };
            }
          }
          
          if (method === 'DELETE') {
            // Delete note from offline storage
            await offlineDb.deleteNote(noteId);
            await offlineDb.saveOfflineChange({
              noteId,
              type: 'delete',
              timestamp: Date.now(),
            });
            return { data: { success: true } };
          }
        }
      }
    }
    return Promise.reject(error);
  }
);

let socket: Socket | null = null;

export const connectSocket = () => {
  const token = localStorage.getItem('token');
  if (!token) return null;

  socket = io('http://localhost:5000', {
    auth: { token },
  });

  return socket;
};

export const getSocket = () => socket;

export const disconnectSocket = () => {
  if (socket) {
    socket.disconnect();
    socket = null;
  }
};

// Sync function
export const syncOfflineChanges = async () => {
  const changes = await offlineDb.getOfflineChanges();
  
  // Sort changes by timestamp
  changes.sort((a, b) => a.timestamp - b.timestamp);
  
  for (const change of changes) {
    try {
      switch (change.type) {
        case 'create':
          if (change.data) {
            await createNote(change.data);
          }
          break;
        case 'update':
          if (change.data) {
            await updateNote(change.noteId, change.data);
          }
          break;
        case 'delete':
          await deleteNote(change.noteId);
          break;
      }
    } catch (error) {
      console.error('Failed to sync change:', change, error);
    }
  }
  
  // Clear offline changes after successful sync
  await offlineDb.clearOfflineChanges();
  
  // Fetch latest notes from server and update local storage
  const response = await getNotes();
  await offlineDb.syncNotes(response.data);
};

// Toggle offline mode
export const toggleOfflineMode = (enabled: boolean) => {
  localStorage.setItem('offlineMode', String(enabled));
};

// Check if we're in offline mode
export const isOfflineMode = () => {
  return localStorage.getItem('offlineMode') === 'true';
};

// Auth API
export const register = (data: { email: string; password: string; name: string }) =>
  api.post('/auth/register', data);

export const login = (data: { email: string; password: string }) =>
  api.post('/auth/login', data);

// Notes API
export const getNotes = () => api.get('/notes');

export const createNote = (data: {
  title: string;
  content: string;
  category?: string;
  tags?: string[];
}) => api.post('/notes', data);

export const updateNote = (
  id: string,
  data: {
    title?: string;
    content?: string;
    category?: string;
    tags?: string[];
    starred?: boolean;
  }
) => api.put(`/notes/${id}`, data);

export const shareNote = (id: string, data: { email: string; canEdit: boolean }) =>
  api.post(`/notes/${id}/share`, data);

export const deleteNote = (id: string) => api.delete(`/notes/${id}`);

// Trash Management
export const getTrash = () => api.get('/notes/trash');

export const moveToTrash = (id: string) => api.put(`/notes/${id}/trash`);

export const restoreFromTrash = (id: string) => api.put(`/notes/${id}/restore`);

export const permanentlyDeleteNote = (id: string) => api.delete(`/notes/${id}`);

export const cleanupOldTrashedNotes = () => api.delete('/notes/trash/cleanup');

// Real-time collaboration
export const joinNoteSession = (noteId: string, userId: string) => {
  if (socket) {
    socket.emit('join:note', { noteId, userId });
  }
};

export const leaveNoteSession = (noteId: string) => {
  if (socket) {
    socket.emit('leave:note', { noteId });
  }
};

export const sendNoteChange = (noteId: string, changes: any) => {
  if (socket) {
    socket.emit('note:change', { noteId, changes });
  }
};

export default api; 