import { openDB, IDBPDatabase } from 'idb';
import { Note } from '../types';

const DB_NAME = 'cloudnotes-offline';
const DB_VERSION = 1;

interface OfflineChange {
  id: string;
  noteId: string;
  type: 'create' | 'update' | 'delete';
  data?: Partial<Note>;
  timestamp: number;
}

class OfflineDB {
  private db: Promise<IDBPDatabase>;

  constructor() {
    this.db = this.initDB();
  }

  private async initDB() {
    return openDB(DB_NAME, DB_VERSION, {
      upgrade(db) {
        // Notes store
        if (!db.objectStoreNames.contains('notes')) {
          db.createObjectStore('notes', { keyPath: 'id' });
        }

        // Offline changes store
        if (!db.objectStoreNames.contains('offlineChanges')) {
          db.createObjectStore('offlineChanges', { keyPath: 'id', autoIncrement: true });
        }
      },
    });
  }

  // Notes operations
  async saveNote(note: Note) {
    const db = await this.db;
    await db.put('notes', note);
  }

  async getNote(id: string) {
    const db = await this.db;
    return db.get('notes', id);
  }

  async getAllNotes() {
    const db = await this.db;
    return db.getAll('notes');
  }

  async deleteNote(id: string) {
    const db = await this.db;
    await db.delete('notes', id);
  }

  // Offline changes operations
  async saveOfflineChange(change: Omit<OfflineChange, 'id'>) {
    const db = await this.db;
    await db.add('offlineChanges', {
      ...change,
      timestamp: Date.now(),
    });
  }

  async getOfflineChanges() {
    const db = await this.db;
    return db.getAll('offlineChanges');
  }

  async clearOfflineChanges() {
    const db = await this.db;
    await db.clear('offlineChanges');
  }

  // Sync operations
  async syncNotes(serverNotes: Note[]) {
    const db = await this.db;
    const tx = db.transaction('notes', 'readwrite');
    const store = tx.objectStore('notes');

    // Delete all existing notes
    await store.clear();

    // Add all server notes
    await Promise.all(serverNotes.map(note => store.add(note)));

    await tx.done;
  }
}

export const offlineDb = new OfflineDB();

// Register sync event listener
if ('serviceWorker' in navigator && 'sync' in window.registration) {
  navigator.serviceWorker.ready.then(registration => {
    registration.sync.register('sync-notes');
  });
} 